.title-carro {
    text-align: center;
}
.productos-detalles-carrito {
    display: flex;
    flex-direction: row;
    margin-bottom: 5%;
}
.producto-unidades {
    display: flex;
    flex-direction: column;
    width: 70%;
    text-align: center;
}
.detalles-carrito {
    display: flex;
    flex-direction: column;
    width: 25%;
    margin-right: 5%;
    margin-top: 5px;
    text-align: center;
    border: 2px solid rgb(60, 71, 60);
    border-radius: 10px;
}
.detalles-carrito-internos {
    display: flex;
    flex-direction: row;
    border-top: 1px solid black;
    margin: 0px 20px;
}
h3 {
    color: gray;
    text-align: start;
    margin: 0;
}
.detalles-carrito-internos{
    display: flex;
    justify-content: space-between;
    color: red;
    font-weight: bold;
    padding: 10px 0px;
}
#boton-terminar-compra {
    padding: 10px 0px;
    background-color: rgb(118, 219, 118);
    border: none;
    cursor: pointer;
    /* color: white; */
    font-size: 1.5rem;
    color: rgb(33, 143, 0);
    border-radius: 0px 0px 10px 10px;
    margin-top: auto;
}
#boton-terminar-compra:focus {
    outline: none;
}
button:hover {
    background-color: rgb(145, 233, 145);
}
.vacio {
    display: flex;
    height: 59vh;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
}
#compra-completada {
    display: flex;
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: rgba(161, 219, 161, 0.897);
    color: rgb(7, 107, 7);
    font-size: 40px;
}
#compra-completada-inv {
    display: none;
}
.pago-invisible {
    display: none;
}
.pago-visible {
    display: flex;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    flex-direction: column;
    background-color: rgba(255, 255, 255, 0.76);
    justify-content: center;
    align-items: center;
}
.contenedor-interno-pago {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    width: 350px;
    height: 250px;
    background-color: rgb(184, 211, 224);
    border-radius: 10px;
    padding: 10px 5px;
}
.btn-camb-tam {
    width: 90%;
    padding: 10px 0;
    margin-top: 10px;
}
.codigo-erroneo {
    background-color: rgba(255, 0, 0, 0.801);
    color: white;
    padding: 5px 10px;
}
@media screen and (max-width: 1024px){
    .detalles-carrito {
        margin-right: 0px;
        width: 28%;
    }
    .detalles-carrito-internos {
        flex-direction: column;
    }
    .detalles-carrito-internos > p {
        width: 100%;
        padding: 0;
        margin: 0;
    }
}
@media screen and (max-width: 425px){
    .productos-detalles-carrito {
        flex-direction: column;
        padding: 0;
        margin: 0;
    }
    .detalles-carrito {
        margin-right: 0px;
        width: 95vw;
        margin: 20px 2vw;

    }
    .detalles-carrito-internos {
        flex-direction: row;
        /* justify-content: center; */
        align-items: center;
        /* text-align: center; */
    }
    .detalles-carrito-internos > p {
        width: auto;
        padding: 0;
        margin: 0;
    }
    .contenedor-interno-pago {
        width: 100%;
        height: 100%;
    }
    #codigo {
        width: 80%;
        margin-top: 10px;
    }
}